            gridColCls: "grid-cols-[repeat(30,20px)]",
            grid: [],
            level: 1,
            score: 0,
            initialLocation: 'A New World',// - ' + (new Date().getTime()),
            initialEvent: 'A New Start',// - ' + (new Date().getTime()),
