                            const p1Words = comboToLock.words.filter(w => w.player === 1).length;
                            const p2Words = comboToLock.words.filter(w => w.player === 2).length;
                            const owner = p1Words > p2Words ? 1 : (p2Words > p1Words ? 2 : data.turn);

