    function stopRecording() {
        if (recorder) {
            if (recorder.recorder) {
                recorder.recorder.stop();
            }
            if (recorder.stream) {
                recorder.stream.getTracks().forEach(track => track.stop());
            }
            if (recorder.motionAnalyzer) {
                recorder.motionAnalyzer.destroy();
            }
            recorder = undefined;
        }
    }

