    function newChatConfig() {
      return {
        peek: false,
        history: undefined,
        pending: false
      };
    }


    function doPeek() {
      if (chatCfg.peek || !inst) {
        return Promise.resolve();
      }

      let c1 = inst.api.promptConfig();
      let c2 = inst.api.chatConfig();
      let p;
      if (c1 && c2) {
        /// Looking up the prompt and chat config to get the full objects
        ///
        chatCfg.peek = true;
        p = new Promise((res, rej) => {
          m.request({ method: 'GET', url: g_application_path + "/rest/chat/config/prompt/" + encodeURIComponent(c1.name), withCredentials: true })
            .then((c) => {
              chatCfg.prompt = c;

              m.request({ method: 'GET', url: g_application_path + "/rest/chat/config/chat/" + encodeURIComponent(c2.name), withCredentials: true }).then((c3) => {
                chatCfg.chat = c3;
                chatCfg.system = c3.systemCharacter;
                chatCfg.user = c3.userCharacter;
                getHistory().then((h) => {
                  chatCfg.peek = true;
                  chatCfg.history = h;
                  m.redraw();
                  res();
                }).catch((e) => {
                  console.warn("Error in chat history", e);
                  chatCfg.peek = false;
                  rej(e);
                });
              }).catch((e) => {
                  console.warn("Error in chat config", e);
                  chatCfg.peek = false;
                  rej(e);
                });
            }).catch((e) => {
              console.warn("Error in prompt config", e);
              chatCfg.peek = false;
              rej(e);
            });
        });
      }
      else {
        console.warn("No prompt or chat config");
        p = Promise.resolve();
      }
      return p;
    }

