        white: [255, 220, 180],
        black: [100, 75, 50],
        asian: [255, 235, 160],
        'East Asian': [255, 235, 160],
        'Southeast Asian': [255, 235, 160],
        indian: [200, 160, 120],
        'middle eastern': [220, 190, 150],
        'latino hispanic': [180, 130, 90]
